export default
{
	methods:{
        getTimeSheetBreakStatus(entity)
		{
            const BreakStatus = {
                Work: 'Work',
                BreakOut: 'BreakOut'
            };

			 if (entity.CheckIn.length == 0)
                return BreakStatus.Work;

            if (entity.CheckOut.length > 0)
                return BreakStatus.Work;

            if (entity.Break1In.length > 0 && entity.Break1Out.length == 0)
                return BreakStatus.BreakOut;

            if (entity.Break2In.length > 0 && entity.Break2Out.length == 0)
                return BreakStatus.BreakOut;

             if (entity.Break3In.length > 0 && entity.Break3Out.length == 0)
                return BreakStatus.BreakOut;

            return BreakStatus.Work;
        },
        getTimeSheetStatusStatus(entity)
        {
            const WorkStatus = {
                Empty: 'Empty',
                Work: 'Work',
                Sick: 'Sick',
                Vacation: 'Vacation',
                Absent: 'Absent',
                Terminate: 'Terminate'
            };

            if (entity.PostIn.length == 0 || entity.PostOut.length == 0)
                return WorkStatus.Empty;

            if (entity.PostIn == "0000" && entity.PostOut == "0000")
                return WorkStatus.Empty;

            if (entity.CheckIn.length == 0 || entity.CheckOut.length == 0)
                return WorkStatus.Work;

            if (entity.PayCode == '041')
                return WorkStatus.Sick;

            if (entity.PayCode == '048')
                return WorkStatus.Vacation;

            if (entity.CheckIn == entity.CheckOut && entity.CheckIn == entity.PostIn)
                return WorkStatus.Absent;

            if (entity.CheckIn == entity.CheckOut && entity.CheckIn == entity.PostOut)
                return WorkStatus.Terminate;

            return WorkStatus.Work;
        }
    }
}
