<template>
  <div>
    <div class="page-header">
      <mt-header fixed :title="employee.LastName + ', ' + employee.FirstName">
        <span slot="left" @click="onShowMenu">
          <font-awesome-icon icon="bars" size="2x" />
        </span>
        <span slot="right" @click="onAddNew">
          <font-awesome-icon icon="plus" size="2x" />
        </span>
      </mt-header>
    </div>

    <div class="page-body">
      <div v-for="(item, index) in listArray" :key="item.HRTimeSheetPK">
        <mt-cell>
          <span slot="title" @click="onDetail(item.HRTimeSheetPK)">
            <span
              class="dateTime"
            >{{item.DateTime|formatDate('MM/dd/yyyy') + ' - ' + item.JobNumber}}</span>
            <br />
            <span class="postInOut">{{item.PostIn + ' - ' + item.PostOut}}</span>
            <br />
            <span class="checkInOut">{{item.CheckIn + ' - ' + item.CheckOut}}</span>
          </span>

          <span class="functions" @click="onCheckInOut(index)">
            <font-awesome-icon icon="camera" size="2x" />
          </span>
          <span class="functions" @click="OnBreak(index)">
            <font-awesome-icon :icon="getBreakIcon(index)" size="2x" />
          </span>
          <span class="functions" @click="onStatus(index)">
            <font-awesome-icon :icon="getStatusIcon(index)" size="2x" />
          </span>
        </mt-cell>
        <hr />
      </div>

      <mt-actionsheet :actions="command.actions" v-model="command.visible" cancelText="Cancel"></mt-actionsheet>

      <mt-popup class="menu" v-model="popupMenu.visible" position="left">
        <ul>
          <li @click="onLogout">
            <font-awesome-icon icon="sign-out-alt" />
            <span>Louout</span>
          </li>
        </ul>
      </mt-popup>
    </div>
  </div>
</template>

<script>
import BizRCHub from "../../api/BizRCHub.js";
import Utils from "../../utils/index.js";

import HelperTimeSheet from "../../mixins/helperTimeSheet.js";

export default {
  name: "EmployeeTimesheetList",
  mixins: [HelperTimeSheet],
  data() {
    return {
      listArray: [],
      employee: {},

      command: {
        actions: [],
        visible: false,
        selectedIndex: 0
      },

      popupMenu: {
        visible: false
      }
    };
  },
  methods: {
    getBreakIcon(index) {
      let status = this.getTimeSheetBreakStatus(this.listArray[index]);
      switch (status) {
        case "Work":
          return "laptop";
        case "BreakOut":
          return "coffee";
        default:
          return "laptop";
      }
    },
    getStatusIcon(index) {
      let status = this.getTimeSheetStatusStatus(this.listArray[index]);
      switch (status) {
        case "Empty":
          return "calendar-times";
        case "Work":
          return "check";
        case "Sick":
          return "hospital";
        case "Vacation":
          return "plane";
        case "Absent":
          return "question";
        case "Terminate":
          return "ban";
        default:
          return "calendar-times";
      }
    },
    getList() {
      if (this.$store.state.employeeTimeSheetList.length > 0) {
        this.listArray = this.$store.state.employeeTimeSheetList;
      } else {
        this.$showLoading();
        BizRCHub.GetEmployeeTimeSheetList(
          this.employee.HREmployeePK,
          this.employee.CurrentDateTime,
          this.employee.MaxDateTime
        )
          .then(res => {
            if (res.data.OK) {
              this.listArray = res.data.Result;
              this.$store.commit("setEmployeeTimeSheetList", this.listArray);
            } else {
              this.$alert(res.data.Error, "Error");
              console.log(res.data.Error);
            }

            this.$closeLoading();
          })
          .catch(err => {
            this.showNetworkError();
            console.log(err);
            this.$closeLoading();
          });
      }
    },
    onAddNew() {
      this.$takeCameraPicture(imgData => {
        this.$store.commit("setPhoto", imgData);
        this.$router.push({
          name: "EmployeeTimesheetDetail",
          params: { PK: "0" }
        });
      });
    },
    onShowMenu() {
      this.popupMenu.visible = true;
    },
    onLogout() {
      Utils.logout();
      this.$router.push({ name: "Login" });
    },
    onDetail(HRTimeSheetPK) {
      this.$router.push({
        name: "EmployeeTimesheetDetail",
        params: { PK: HRTimeSheetPK }
      });
    },
    checkInOut(entity) {
      this.$takeCameraPicture(imgData => {
        this.$showLoading();
        BizRCHub.EmployeeClock(entity.HRTimeSheetPK, imgData)
          .then(res => {
            if (res.data.OK) {
              let item = res.data.Result;
              Object.assign(entity, item);
            } else {
              this.$alert(res.data.Error, "Error");
              console.log(res.data.Error);
            }
            this.$closeLoading();
          })
          .catch(err => {
            this.showNetworkError();
            console.log(err);
            this.$closeLoading();
          });
      });
    },
    onCheckInOut(index) {
      let entity = this.listArray[index];
      try {
        navigator.geolocation.getCurrentPosition(
          pos => {
            this.$showLoading();
            console.log(pos.coords);
            BizRCHub.IsInJobSite(
              entity.JobNumber,
              pos.coords.latitude,
              pos.coords.longitude
            )
              .then(res => {
                this.$closeLoading();
                if (res.data.OK) {
                  this.checkInOut(entity);
                } else {
                  this.$alert("You are not near by the job site.", "Error");
                  console.log(res.data.Error);
                }
              })
              .catch(err => {
                this.showNetworkError();
                console.log(err);
                this.$closeLoading();
              });
          },
          err => {
            this.$alert("The location service must be enabled.", "Error");
            console.log(err);
          },
          { timeout: 1000, enableHighAccuracy: true, maximumAge: 3000 }
        );
      } catch (error) {
        this.$alert("The location service must be enabled.", "Error");
      }
    },
    OnBreak(index) {
      this.$showLoading();
      let entity = this.listArray[index];
      BizRCHub.EmployeeBreak(entity.HRTimeSheetPK)
        .then(res => {
          if (res.data.OK) {
            let item = res.data.Result;
            Object.assign(entity, item);
          } else {
            this.$alert(res.data.Error, "Error");
            console.log(res.data.Error);
          }
          this.$closeLoading();
        })
        .catch(err => {
          this.showNetworkError();
          console.log(err);
          this.$closeLoading();
        });
    },
    onStatus(index) {
      this.command.selectedIndex = index;

      this.command.actions = [];
      this.command.actions.push({
        name: "Mark As Sick",
        method: this.MarkAsSick
      });
      this.command.actions.push({
        name: "Mark As Vacation",
        method: this.MarkAsVacation
      });
      this.command.actions.push({
        name: "Mark As Work",
        method: this.MarkAsWork
      });
      this.command.actions.push({
        name: "Mark As Absent",
        method: this.MarkAsAbsent
      });
      this.command.actions.push({
        name: "Mark As Terminate",
        method: this.MarkAsTerminate
      });

      let status = this.getTimeSheetStatusStatus(this.listArray[index]);
      switch (status) {
        case "Work": {
          this.command.actions.splice(2, 1);
          break;
        }
        case "Sick": {
          this.command.actions.splice(0, 1);
          break;
        }
        case "Vacation": {
          this.command.actions.splice(1, 1);
          break;
        }
        case "Absent": {
          this.command.actions.splice(3, 1);
          break;
        }
        case "Terminate": {
          this.command.actions.splice(4, 1);
          break;
        }
      }
      this.command.visible = true;
    },
    MarkAsSick() {
      this.$showLoading();
      let entity = this.listArray[this.command.selectedIndex];
      BizRCHub.EmployeeMarkAsSick(entity.HRTimeSheetPK)
        .then(res => {
          if (res.data.OK) {
            let item = res.data.Result;
            Object.assign(entity, item);
          } else {
            this.$alert(res.data.Error, "Error");
            console.log(res.data.Error);
          }
          this.$closeLoading();
        })
        .catch(err => {
          this.showNetworkError();
          console.log(err);
          this.$closeLoading();
        });
    },
    MarkAsVacation() {
      this.$showLoading();
      let entity = this.listArray[this.command.selectedIndex];
      BizRCHub.EmployeeMarkAsVacation(entity.HRTimeSheetPK)
        .then(res => {
          if (res.data.OK) {
            let item = res.data.Result;
            Object.assign(entity, item);
          } else {
            this.$alert(res.data.Error, "Error");
            console.log(res.data.Error);
          }
          this.$closeLoading();
        })
        .catch(err => {
          this.showNetworkError();
          console.log(err);
          this.$closeLoading();
        });
    },
    MarkAsWork() {
      this.$showLoading();
      let entity = this.listArray[this.command.selectedIndex];
      BizRCHub.EmployeeMarkAsWork(entity.HRTimeSheetPK)
        .then(res => {
          if (res.data.OK) {
            let item = res.data.Result;
            Object.assign(entity, item);
          } else {
            this.$alert(res.data.Error, "Error");
            console.log(res.data.Error);
          }
          this.$closeLoading();
        })
        .catch(err => {
          this.showNetworkError();
          console.log(err);
          this.$closeLoading();
        });
    },
    MarkAsAbsent() {
      this.$showLoading();
      let entity = this.listArray[this.command.selectedIndex];
      BizRCHub.EmployeeMarkAsAbsent(entity.HRTimeSheetPK)
        .then(res => {
          if (res.data.OK) {
            let item = res.data.Result;
            Object.assign(entity, item);
          } else {
            this.$alert(res.data.Error, "Error");
            console.log(res.data.Error);
          }
          this.$closeLoading();
        })
        .catch(err => {
          this.showNetworkError();
          console.log(err);
          this.$closeLoading();
        });
    },
    MarkAsTerminate() {
      this.$prompt("Please input reason")
        .then(({ value, action }) => {
          this.$showLoading();
          let entity = this.listArray[this.command.selectedIndex];
          BizRCHub.EmployeeMarkAsTerminate(entity.HRTimeSheetPK, value)
            .then(res => {
              if (res.data.OK) {
                let item = res.data.Result;
                Object.assign(entity, item);
              } else {
                this.$alert(res.data.Error, "Error");
                console.log(res.data.Error);
              }
              this.$closeLoading();
            })
            .catch(err => {
              this.showNetworkError();
              console.log(err);
              this.$closeLoading();
            });
        })
        .catch(err => {});
    }
  },
  mounted() {
    this.employee = Utils.getUserSettings();
    this.getList();
  }
};
</script>

<style scoped>
.page-header {
  margin-bottom: 40px;
}
li {
  margin-bottom: 20px;
  list-style-type: none;
}
.menu {
  width: 50%;
  height: 100%;
  overflow: auto;
}
.dateTime {
  display: inline-block;
  margin-bottom: 5px;
}
.postInOut {
  display: inline-block;
  margin-bottom: 5px;
}
.checkInOut {
  display: inline-block;
}
.functions {
  color: red;
  margin-right: 10px;
}
</style>
